import { useEffect, useState } from "react";
import DashboardWrapper from "../../../components/dashboard";
import { useNavigate } from "react-router-dom";
import AdminButton from "../components/adminButton";
import UserAdmin from "../user-admin/user-admin";
import VehicleAdmin from "../vehicle-admin/vehicle-admin";
import FleetTagAdmin from "../fleet-tag-admin/fleet-tag-admin";
import DriverAdmin from "../driver-admin/driver-admin";
import { useTranslation } from "react-i18next";
import { admins } from "./constants";
import HeaderView from "../../../components/admin/headerView";
import { APP_CONFIG } from "../../../constants/constants";

const ScreenAdminView = () => {
  const [selectedAdminButton, setSelectedAdminButton] = useState<string>(
    admins.user_admin
  );

  const [ currentComponent, setCurrentComponent ] = useState(<UserAdmin />);
  const { t } = useTranslation('translation', { keyPrefix: 'adminViewScreen'});

  const handleButtonClick = (buttonText: string) => {
    setSelectedAdminButton(buttonText);
  };

  useEffect(() => {
    switch(selectedAdminButton) {
      case admins.user_admin:
        setCurrentComponent(<UserAdmin />);
        break;
      case admins.vehicle_admin:
        setCurrentComponent(<VehicleAdmin />);
        break;
      case admins.fleet_tag_admin:
        setCurrentComponent(<FleetTagAdmin />);
        break;
      case admins.driver_admin:
        setCurrentComponent(<DriverAdmin />);
        break;
      default:
        setCurrentComponent(<UserAdmin />);
    }
  }, [selectedAdminButton])

  return (
    <DashboardWrapper>
      <HeaderView title={t('admin_view')} />
      <div className={`flex ${APP_CONFIG.DES.DASH.P_HORIZ} py-2 mt-2`}>
        <div className="bg-blue-200 p-2 space-x-2 rounded-lg">
          <AdminButton
            text={t(admins.user_admin)}
            onClick={() => handleButtonClick(admins.user_admin)}
            selected={selectedAdminButton === admins.user_admin}
          />
          <AdminButton
            text={t(admins.vehicle_admin)}
            onClick={() => handleButtonClick(admins.vehicle_admin)}
            selected={selectedAdminButton === admins.vehicle_admin}
          />
          <AdminButton
            text={t(admins.fleet_tag_admin)}
            onClick={() => handleButtonClick(admins.fleet_tag_admin)}
            selected={selectedAdminButton === admins.fleet_tag_admin}
          />
          <AdminButton
            text={t(admins.driver_admin)}
            onClick={() => handleButtonClick(admins.driver_admin)}
            selected={selectedAdminButton === admins.driver_admin}
          />
        </div>
      </div>
      <main>
        {currentComponent}
      </main>
    </DashboardWrapper>
  );
};

export default ScreenAdminView;
