type TAppEnv = "local_ngrok" | "dev" | "prod";

export const APP_ENV: TAppEnv = "local_ngrok"; // change this to "dev" or "prod" before deploying

let _API_SERVER_URL = "";
switch (APP_ENV as TAppEnv) {
  case "local_ngrok":
    _API_SERVER_URL = localStorage.getItem('TEMP_API_SERVER') || "https://6a0c-2401-4900-1c68-a1e2-a39a-dae4-ff8f-c167.ngrok-free.app"; // TODO: Remove this line before deploying to production
    break;
  case "dev":
    _API_SERVER_URL = "https://fleettrackdev-47748.botics.co";
    break;
  case "prod":
  default:
    _API_SERVER_URL = "https://fleettrackprod-47748.botics.co";
    break;
}

export const API_SERVER_URL = _API_SERVER_URL;
