import { FC, useState } from "react";

type AdminFormFieldInputProps = {
  label: string;
  type: string;
  id: string;
  name: string;
  placeholder?: string;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onBlur?: (e: React.FocusEvent<HTMLInputElement>) => void;
  value?: string;
  touched?: boolean;
  error?: string;
  disabled?: boolean;
  customInputClass?: string;
};

export const AdminFormFieldInput: FC<AdminFormFieldInputProps> = ({
  label,
  type,
  id,
  name,
  placeholder = "",
  onChange,
  onBlur,
  value,
  touched,
  error,
  disabled = false,
  customInputClass = "",
}) => {
  let wrapperClass = touched && !!error ? "" : "";
  let labelClass =
    touched && !!error ? "text-field-error-dark" : "text-field-label-valid";
  let inputClass =
    touched && !!error
      ? "border-field-error-border focus-visible:outline-field-error-dark text-field-error-dark"
      : "border-gray-200 text-field-label-valid";
  const floatingError = false;

  return (
    <div
      className={`col-span-6${
        floatingError ? ` relative pb-4` : ""
      } ${wrapperClass}`}
    >
      <label
        htmlFor={id}
        className={`block text-sm font-display font-semibold ${labelClass}`}
      >
        {label}
      </label>

      <input
        type={type}
        id={id}
        name={name}
        placeholder={placeholder}
        onChange={onChange}
        onBlur={onBlur}
        value={value}
        disabled={disabled}
        className={`mt-1 w-full h-11 px-3 rounded-md bg-white text-sm shadow-sm focus-visible:outline-4 focus-visible:shadow-none${
          touched ? " touched" : ""
        } ${disabled ? "bg-gray-400" : ""} ${inputClass} ${customInputClass}`}
      />
      {touched && !!error && (
        <p
          className={`${
            floatingError ? "absolute" : ""
          } bg-field-error-light px-2 py-1 rounded text-field-error-dark text-xs mt-1`}
        >
          {error}
        </p>
      )}
    </div>
  );
};

type AdminFormFieldDropdownProps = {
  label: string;
  id: string;
  name: string;
  value: string;
  onChange: (e: React.ChangeEvent<HTMLSelectElement>) => void;
};

export const AdminFormFieldDropdown: FC<AdminFormFieldDropdownProps> = ({
  label,
  id,
  name,
  value,
  onChange,
}) => {

  const [selected, setSelected] = useState(value);
  const handleChange = (e:any) => {
    setSelected(e.target.value);
    onChange(e)
  }

  return (
    <div className="col-span-6">
      <label
        htmlFor={id}
        className={`block text-sm font-display font-semibold text-field-label-valid`}
      >
        {label}
      </label>
      <select
        value={selected}
        onChange={(e) => handleChange(e)}
        className="mt-1 w-full h-11 px-3 rounded-md bg-white text-sm shadow-sm focus-visible:outline-4 focus-visible:shadow-none border-gray-200"
      >
        <option value="option1">Option 1</option>
        <option value="option2">Option 2</option>
        <option value="option3">Option 3</option>
      </select>
    </div>
  );
};

export const AdminFormFieldCheckbox: FC<AdminFormFieldInputProps> = ({
  label,
  id,
  type,
  name,
  value,
  onChange,
  onBlur,
}) => {

  const [checked, setChecked] = useState(value === "true" ? true : false);

  const handleChange = (e:any) => {
    setChecked(e.target.checked);
    onChange?.(e)
  }
  
  return (
    <div className="bg-white col-span-4 flex justify-between items-center p-3 rounded-lg shadow-sm border-gray-200 cursor-pointer">
      <label
        className={`block text-sm font-display font-semibold text-field-label-valid`}
      >
        {label}
      </label>
      <div>
        <input
          type={type}
          id={id}
          name={name}
          checked={checked}
          value={value}
          onChange={handleChange}
          onBlur={onBlur}
          className="mt-1 w-full h-11 px-3 rounded-md text-sm shadow-sm sr-only peer focus-visible:outline-4 focus-visible:shadow-none"
        />
        <div onClick={() => handleChange({ target: { checked: !checked, value: !checked ? 'true' : 'false', name: name } })} className="relative w-12 h-6 bg-gray-200 rounded-full peer-checked:after:translate-x-full rtl:peer-checked:after:-translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:start-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-green-500"></div>
      </div>
    </div>
  );
};
