import { FC } from "react";


interface AdminButtonProps {
  text: string;
  onClick: () => void;
  selected: boolean;
}

const AdminButton: FC<AdminButtonProps> = ({ text, onClick, selected }) => (
  <button
    className={`p-4 font-normal text-base leading-4 text-left hover:bg-white rounded-lg ${
      selected ? "bg-white shadow-lg font-bold" : ""
    }`}
    onClick={onClick}
  >
    {text}
  </button>
);

export default AdminButton;