import React from "react";
import Logo from "../assets/svg/logo-fleettrack.svg";
import MapOverviewIcon from "../assets/svg/map-overview-icon.svg";
import DashboardIcon from "../assets/svg/dashboard-icon.svg";
import ReportsIcon from "../assets/svg/reports-icon.svg";
import ManageRolesIcon from "../assets/svg/manage-roles-icon.svg";
import MaintenanceAdminIcon from "../assets/svg/maintainance-admin-icon.svg";
import GroupAdminIcon from "../assets/svg/group-admin-icon.svg";
import ManageRuleIcon from "../assets/svg/manage-rule-icon.svg";
import CameraAdminIcon from "../assets/svg/camera-admin-icon.svg";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { routeUrls } from "../navigation/routeUrls";
import { useLogoutMutation } from "../api/network/userApiService";
import { sideEffectLogOut } from "../utils/common";

interface ListItemProps {
  active?: boolean;
  icon: string;
  text: string;
}

const ListItem: React.FC<ListItemProps> = ({ active = false, icon, text }) => (
  <li className={`p-3 rounded-lg ${active ? "bg-accent-blue-pale" : "hover:bg-gray-300 cursor-pointer"}`}>
    <span className="flex items-center gap-2">
      <img src={icon} alt={text} />
      <p className={`font-bold text-xs ${active ? "text-accent-blue-dark" : "text-gray-500"}`}>{text}</p>
    </span>
  </li>
);

const Sidemenu = () => {
  const { t } = useTranslation("translation", { keyPrefix: "dashboard.sidemenu" });
  const dashboardMenuItemSlugs = [
    "dashboard",
    "map_overview",
    "reports",
    "manage_roles",
    "maintenance_admin",
    "group_admin",
    "manage_rule_alert",
    "camera_admin",
  ];
  const activeDashboardMenuItem = dashboardMenuItemSlugs[0]; // TODO: make this dynamic
  const dashboardMenuItems = [
    { slug: dashboardMenuItemSlugs[0], icon: DashboardIcon },
    { slug: dashboardMenuItemSlugs[1], icon: MapOverviewIcon },
    { slug: dashboardMenuItemSlugs[2], icon: ReportsIcon },
    { slug: dashboardMenuItemSlugs[3], icon: ManageRolesIcon },
    { slug: dashboardMenuItemSlugs[4], icon: MaintenanceAdminIcon },
    { slug: dashboardMenuItemSlugs[5], icon: GroupAdminIcon },
    { slug: dashboardMenuItemSlugs[6], icon: ManageRuleIcon },
    { slug: dashboardMenuItemSlugs[7], icon: CameraAdminIcon },
  ];

  const [logoutAPITrigger, { isLoading }] = useLogoutMutation()

  const handleLogout = () => {
    logoutAPITrigger()
      .then(() => {
        sideEffectLogOut()
      })
  }

  return (
    <div className="flex flex-col bg-gray-100 px-8 py-6 h-full gap-6">
      <Link to={routeUrls.dashboard}>
        <img src={Logo} alt="fleet-track-logo" />
      </Link>
      <ul className="space-y-4 flex-grow overflow-auto">
        {dashboardMenuItems.map((item, index) => (
          <ListItem key={index} icon={item.icon} text={t(item.slug)} active={item.slug === activeDashboardMenuItem} />
        ))}
      </ul>
      <button
        className="bg-blue-200 text-blue-800 font-medium text-base leading-6 py-2 px-2 rounded-full w-full"
        disabled={isLoading}
        onClick={handleLogout}>
        {t("logout")}
      </button>
    </div>
  );
};

export default Sidemenu;
